.g-btn {
  color: #fff;
  background-color: #fd6400;
  border-color: #fd6400;
}

.mat-form-field {
  width: 100%;
}

.mat-expansion-panel:not([class*="mat-elevation-z"]) {
  box-shadow: none;
}

.mat-expansion-panel {
  background: transparent;
  color: rgba(0, 0, 0, 0.87);
}

.mat-card {
  height: 100%;
}

.btn-secondary:not(:disabled):not(.disabled).active,
.btn-secondary:not(:disabled):not(.disabled):active,
.show>.btn-secondary.dropdown-toggle {
  color: #fff;
  background-color: #fd6400;
  border-color: #fd6400;
}

.btn-secondary {
  min-width: 124px;
}

.btn-group-toggle .btn-secondary {
  color: #fff;
  background-color: #a1a1a1;
  border-color: transparent;
  border-radius: 5px !important;
  padding: 5px 8px;
  font-size: 13.5px;
  font-weight: 400;
}

.example-container {
  height: calc(100vh - 380px);
  padding: 12px;
  overflow: auto;
}

table {
  width: 100%;
}

.select-outer {
  float: right;
}

.color1 {
  background: #0eab1c;
  height: 28px !important;
  display: inline-block;
  align-items: flex-end;
  vertical-align: middle;
  width: 5px;
  margin-right: 17px;
}

.completed {
  border-left: 5px solid #0eab1c;
}

.pending {
  border-left: 5px solid #969696;
}

.cancelled {
  border-left: 5px solid red;
}

.sort {
  width: 170px;
  float: right;
  font-family: "GothamNarrowBold", sans-serif;
}

.sort-text {
  min-width: 70px;
  font-size: 11px;
  margin-right: 5px;
  text-align: right;
}

.sort .mat-form-field-appearance-outline .mat-form-field-infix {
  padding: 1px 2px 6px 1px !important;
  font-size: 12px;
}

.sort .mat-raised-button.mat-warn {
  background-color: #fd6400;
}

.right-filter .mat-raised-button.mat-warn {
  box-shadow: 0 0 6px 1px rgba(255, 100, 0, 0.29) !important;
  background-color: #ff6400 !important;
  min-width: 161px;
}

.any-src {
  padding-right: 16px;
  text-align: left;
  white-space: nowrap;
}

.synch {
  font-size: 12px;
  margin-right: 15px;
}

.all {
  margin-left: 13px;
  font-weight: bold;
  font-size: 14px;
  font-family: "GothamNarrowBold", sans-serif;
}

.head1 {
  text-align: left !important;
  font-size: 17px;
}

.patient-name {
  font-weight: 400;
  color: #0075bc;
  font-family: "GothamNarrowBold", sans-serif;
}

.example-container table tr th:nth-of-type(2),
.example-container table tr td:nth-of-type(2).example-container table tr th:nth-of-type(3),
.example-container table tr td:nth-of-type(3) {
  min-width: 25% !important;
}

.imgouter {
  width: 42px;
  text-align: center;
}

.norecord {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 10px;
  border: 1px solid #ccc9;
  border-top: transparent;
  background-color: #fbfbfb;
}

td.mat-cell:last-of-type,
td.mat-footer-cell:last-of-type,
th.mat-header-cell:last-of-type {
  padding-right: 12px;
}

.example-container tr td:last-child,
.example-container tr th:last-child {
  max-width: 200px;
  width: 200px;
  text-align: left;
}

table tr th:first-child {
  border-radius: 5px 0 0 0px !important;
  padding-left: 30px;
}

table tr th:last-child {
  border-radius: 0px 5px 0 0px !important;
}

.example-container1 {
  height: calc(100vh - 90px);
  margin-top: 70px;
  position: relative;
  z-index: 0;
}

.example-sidenav {
  padding: 20px;
}

.dashboard-wrapper {
  padding-left: 0 !important;
}

.ipadfiler {
  display: none;
}

.example-sidenav {
  padding: 0;
}

.dashboard-wrapper {
  padding-left: 0 !important;
}

@media (max-width: 1025px) {
  .dashboard-wrapper {
    padding-left: 12px !important;
    padding-right: 12px !important;
  }

  .btn-secondary {
    min-width: auto;
  }

  .toprow {
    flex-direction: row;
    padding-right: 0 !important;
  }

  .ipadfiler {
    display: block;
  }

  .example-sidenav {
    padding: 0;
  }

  .mat-card {
    border-radius: 10px !important;
  }

  .ipadfiler button {
    min-width: 48px;
    padding: 0;
    margin-left: 10px;
  }
}

.plist-date {
  font-size: 11px;
  width: 77px;
  word-break: break-all;
  white-space: normal;
  text-align: left;
  display: block;
}

table.mat-table {
  border-collapse: separate !important;
  border-spacing: 0 12px !important;
  margin-top: -12px !important;
}

table.mat-table tbody tr {
  border-radius: 10px !important;
  box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.19) !important;
}

tr.mat-footer-row,
tr.mat-row {
  height: 60px;
  border-radius: 10px !important;
  box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.19) !important;
}

table tr td:last-child {
  text-align: center;
}

table tr th:last-child {
  padding-right: 30px !important;
}

.patient-table tr td {
  background: #fff !important;
  border-radius: 8px !important;
  font-size: 14px !important;
}
.patient-table tr th {
  font-size: 15px !important;
}

.col {
  min-width: 0;
  max-width: 100%;
  padding: 0;
  font-size: 15px !important;
}

.head1 {
  color: #000;
  font-size: 15px !important;
  font-family: "GothamNarrowBold", sans-serif;
  font-weight: bold;
}

.heading {
  margin: 0;
  font-size: 18px;
  font-family: "GothamNarrowBold", sans-serif;
  font-weight: bold;
  margin-left: 14%;
}

.pdate {
  width: 75px;
  display: block;
  word-break: break-word;
}

.c-point {
  cursor: pointer;
}

#print-btn {
  line-height: 34px;
  border-radius: 50% !important;
  height: 45px !important;
  width: 45px !important;
  background: #1072ce;
  z-index: 9;
}

.mat-icon {
  color: #fff !important;
}

.page-content {
  height: 100%;
}
.page-content .mat-card {
  background: transparent !important;
}
.topHead {
  background: #fff;
  margin: 0 15px;
  border-radius: 9px;
  padding: 15px 0 15px 0;
}
.belowpart {
  background: #fff;
  margin: 0 15px;
  border-radius: 9px;
  padding: 0px 0 15px 0;
}
.belowpart p {
  padding-top: 15px;
}
.centerHead {
  font-family: gothambook, sans-serif;
  background: #fff;
  padding: 14px 115px;
  border-radius: 25px;
  font-size: 20px;
  font-weight: 700;
}
.patientHead p {
  font-size: 20px;
  border-bottom: 1px solid #ccc;
  font-family: GothamNarrowBold, sans-serif;
  padding-bottom: 5px;
}